import React, { useState } from 'react'
import {useHistory} from 'react-router-dom'
import './Tutorial.css'
import trash from './trash.svg';
import rubber from './rubber.svg';
import source from './source.svg';
import dest from './dest.svg';

export default function Tutorial() {
    const [pageNumber, setPageNumber]  = useState(1);
    const history = useHistory();

    const handleNextPage = (currentPage) => {
        setPageNumber(currentPage+1);
        if(pageNumber === 3){
            history.push("/playground");
        }
    }

    const handleSkipPage = () => {
        history.push("/playground");
    }

    return (
        <>
        {pageNumber === 1 && 
            <div className="dialog-box">
                <div className="special">
                    <p className='msgs'>
                        Hi! <br></br>
                        Welcome to <br></br>
                    </p>
                    <p className='highlight'>Path Finder</p>
                </div>
                
                <div className='buttons'>
                    <button className="skip" onClick={() => handleSkipPage()}>Skip</button>
                    <button className="next" onClick={() => handleNextPage(pageNumber)}>Next</button>
                </div>
            </div>
        }

        {pageNumber === 2 && 
            <div className="dialog-box">
                <div className="available">
                    <div className="algo">
                        <p className='highlight'>Search Algorithms</p>
                        <p className='msg'>
                            Breadth First Search <br></br>
                            Depth First Search
                        </p>
                        <p className = "imp-note">
                            For creating obstables click once <br></br>
                            to start drawing on the grid <br></br> 
                            and again click to stop. In between<br></br>
                            hover to draw.
                        </p>

                    </div>
                    <div className="mazealgo">
                        <p className='highlight'>Ways to create <br></br>
                        Obstacles <br></br>
                        </p>
                        <p className='msg'>
                            Recursive Division <br></br>
                            Horizontal Skew <br></br>
                            Vertical Skew <br></br>
                            Random Maze <br></br>
                            Mouse Press on the Grid <br></br>
                        </p>
                    </div>
                </div>
                <div className='buttons'>
                    <button className="skip" onClick={() => handleSkipPage()}>Skip</button>
                    <button className="next" onClick={() => handleNextPage(pageNumber)}>Next</button>
                </div>
            </div>
        }

        {pageNumber === 3 && 
            <div className="dialog-box">
                <div className="menu">
                    <div className="icons">
                        <img src={source} alt="source" height="50px"/>   
                        <img src={dest} alt="dest" height="50px"/> 
                        <div className="background">
                            <img src={trash} alt="trash" height="40px"/>
                        </div>
                        <div className="background-blocked">
                            <img src={trash} alt="trash" height="40px"/>
                        </div>
                        <div className="background">
                            <img src={rubber} alt="rubber" height="40px"/>
                        </div>
                        <div className="background-blocked">
                            <img src={rubber} alt="rubber" height="40px"/>
                        </div>
                    </div>

                    <div className="text">
                        <p className='msgs'> Source </p>
                        <p className='msgs'> Destination </p>
                        <p className='msgs'> Clears the entire Grid </p>
                        <p className='msgs'> Currently blocked </p>
                        <p className='msgs'> Clears the Visited paths </p>
                        <p className='msgs'> Currently blocked </p>
                    </div>
                </div>
                <div className='buttons'>
                    <button className="skip" onClick={() => handleSkipPage()}>Skip</button>
                    <button className="next" onClick={() => handleNextPage(pageNumber)}>Next</button>
                </div>
            </div>
        
            }

        </>
    )
}
