import logo from './logo.svg';
import trash from './trash.svg';
import eraser from './rubber.svg';
import {Link} from 'react-router-dom';
import './Navbar.css'

export default function Navbar(props) {
    
    const handleChangeAlgo = (algorithm) => {
        props.changeAlgo(algorithm);
        var changeButton = document.getElementById('5');
        
        changeButton.textContent = "Visualize "  + algorithm;  
        if(changeButton.style.width !== '120px'){
            changeButton.style.width = '160px';
        }
    }

    const handleVisualize = () => {
        if(props.algo === '' && props.mazeAlgo === ''){
            var changeButton = document.getElementById('5');
            changeButton.textContent = "Select an Algorithm !";
            changeButton.style.width = '220px';
        }
        else{
            if(props.algo === 'BFS'){
                props.handleBFS();
            }
            else if(props.algo === 'DFS'){
                props.handleDFS();
            }
            else if(props.algo === 'Astar'){
                props.handleDFS();
            }
        }       
    }
    
    window.onclick = function(event) {
        if (!event.target.matches('.algobutton')) {
            var dropdowns = document.getElementsByClassName("algorithms");
            for (let i = 0; i < dropdowns.length; i++) {
                var openDropdown = dropdowns[i];
                if (openDropdown.classList.contains('show')) {
                    openDropdown.classList.remove('show');
                }
            }
        }
        if (!event.target.matches('.mazebutton')) {
            dropdowns = document.getElementsByClassName("maze-algorithms");
            for (let i = 0; i < dropdowns.length; i++) {
                openDropdown = dropdowns[i];
                if (openDropdown.classList.contains('show')) {
                    openDropdown.classList.remove('show');
                }
            }
        }
    }
   
    const handleClickAlgo = () => {
        document.getElementById('algo').classList.toggle("show");
    }

    const handleClickMaze = () => {
        document.getElementById('mazealgo').classList.toggle("show");
    }

    return (
        <>
        <nav className="dropdown">
            <div className="logo">
                <Link to="/">
                    <img src={logo} alt="logo"  height="50px"/>
                </Link>
            </div>
            <h1 className="title">Path Finder</h1>
            <div className = "Algorithms">
                <button className="algobutton" id='1' onClick = {() => handleClickAlgo()}> Algorithms </button>
                
                <ul className = "algorithms" id='algo'>
                    <li className = "bfs" onClick = {() => handleChangeAlgo("BFS")}>Breadth First Search</li>
                    <li className = "dfs" onClick = {() => handleChangeAlgo("DFS")}>Depth First Search</li>
                </ul>
            </div>
 
            <div className = "MazeAlgorithms">
                <button className="mazebutton" id='2' onClick = {() => handleClickMaze()}>Maze Algorithms</button >
                <ul className = "maze-algorithms" id='mazealgo'>
                    <li className = "rd" onClick = {() => props.handleMazeGeneration()}>Recursive Division</li>
                    <li className = "vs" onClick = {() => props.handleVerticalSkew()}>Vertical Skew</li>
                    <li className = "hs" onClick = {() => props.handleHorizontalSkew()}>Horizontal Skew</li>
                    <li className = "rm" onClick = {() => props.handleRandomMazeGenerate()}>Random Maze</li>
                </ul>
            </div>
            <div className="infos" id='info'>
                <input type="image" src={eraser} alt="eraser" height = "25px" className = "info-disabled-path" id='3' onClick={() => props.handleVisitedReset()}></input>
                <input type="image" src={trash} alt="trash" height = "25px" className = "info-disabled" id='4' onClick={() => props.handleCompleteReset()}></input>
            </div>
            <button className = "visual" onClick = {() => handleVisualize()} id='5'>Visualize</button>
        </nav>
        </>
    )
}