import React from 'react'
import { useState } from 'react';
import pageNotFound from './page-not-found.svg';
import Navbar from '../Navbar/Navbar';
import Grid from '../Grid/Grid';
import Footer from '../Footer/Footer'
import Queue from '../queue';
import './Main.css'

const n = 23, m = 50;

const dx = [0, 0, 1, -1];
const dy = [1, -1, 0, 0];

export default function Main() {
    // properites of navbar

    const [algo, setAlgo] = useState('');
    const [mazeAlgo, setMazeAlgo] = useState('');

    //properties of the grid

    const [grid, setGrid] = useState(createGrid(n, m));
    const [isVisualised, setIsVisualised] = useState(false);
    const [isMazeVisualised, setIsMazeVisualised] = useState(false);
    const [distance, setDistance] = useState(Infinity);
    const [running, setRunning] = useState(false);
    const [reached, setReached] = useState(false);
    const [src, setSrc] = useState({x: Math.floor(n/2), y: 3});
    const [dest, setDest] = useState({x: Math.floor(n/2), y: m-4});
    const [alert, setAlert] = useState(false);

    var visited = createGrid(n, m);
    var parent = createGrid(n, m);


    if(running){
        let button1 = document.getElementById('1');
        let button2 = document.getElementById('2');
        let button5 = document.getElementById('5');

        button1.disabled = true;
        button1.removeAttribute('algobutton');
        button1.className = 'buttonDisabled'
    
        button2.disabled = true;
        button2.removeAttribute('mazebutton');
        button2.className = 'buttonDisabled'
       
        button5.disabled = true;
        button5.textContent = "Locked !"
        button5.removeAttribute('visual');
        button5.className = 'disabled'
    }

    if(isVisualised){
        let button3 = document.getElementById('3');
        button3.className = 'pathReset';
        button3.disabled = false;
    }
    else{
        let button3 = document.getElementById('3');
        if(button3) {
            button3.removeAttribute('pathReset');
            button3.className = 'info-disabled-path';
            button3.disabled = true;
        }
    }
   
    if(isMazeVisualised && running === false){
        let button4 = document.getElementById('4');
        button4.className = 'gridReset'
        button4.disabled = false;
    }
    else{
        let button4 = document.getElementById('4');
        if(button4) {
            button4.removeAttribute('gridReset');
            button4.className = 'info-disabled';
            button4.disabled = true;
        }
    } 

    const restore_buttons = () => {
        var button1 = document.getElementById('1');
        var button2 = document.getElementById('2');
        var button5 = document.getElementById('5');
        
        button1.disabled = false;
        button1.className = 'algobutton'

        button2.disabled = false;
        button2.className = 'mazebutton'
     
        button5.disabled = false;
        button5.className = 'visual';
        button5.textContent = "Visualize"
    }
      
    const changeAlgo = (newAlgo) => {
        setAlgo(newAlgo);
    }

    const changeMazeAlgo = (newAlgo) => {
        setMazeAlgo(newAlgo);
    }


    for (let i = 0; i < n; i++) {
        for (let j = 0; j < m; j++) {
            parent[i][j] = {
                par_x: -1,
                par_y: -1
            };
        }
    }

    const color = (r, c) => {
        var copy = [...grid];
        copy[r][c] = 1;
        setGrid(copy);
    }

    const retrack = (r, c) => {
        var copy = [...grid];
        copy[r][c] = 2;
        setGrid(copy);
    }

    const wall = (r, c) => {
        var copy = [...grid];
        copy[r][c] = 3;
        setGrid(copy);
    }

    const handleVisitedReset = function () {
        var copy = [...grid];
       
        for (let i = 0; i < n; i++) {
            for (let j = 0; j < m; j++) {
                if (grid[i][j] !== 3) {
                    copy[i][j] = 0;
                }
            }
        }

        setGrid(copy);
        parent = createGrid(n, m);

        for (let i = 0; i < n; i++) {
            for (let j = 0; j < m; j++) {
                parent[i][j] = {
                    par_x: -1,
                    par_y: -1
                };
            }
        }
        setIsVisualised(false);
        setDistance(Infinity);
        setReached(false);
    }

    const handleCompleteReset = function () {
        var copy = [...grid];
        for (let i = 0; i < n; i++) {
            for (let j = 0; j < m; j++) {
                copy[i][j] = 0;
            }
        }
        setGrid(copy);
        parent = createGrid(n, m);

        for (let i = 0; i < n; i++) {
            for (let j = 0; j < m; j++) {
                parent[i][j] = {
                    par_x: -1,
                    par_y: -1
                };
                visited[i][j] = 0;
            }
        }
        setIsVisualised(false);
        setIsMazeVisualised(false);
        setDistance(Infinity);
        setReached(false);
    }

    function evenRandomNumberInRange(a, b) {
        var x = Math.floor(Math.random()*(b-a)+a);
        if(x%2===0){
            return x;
        }
        else{
            if(x+1<=b) return x+1;
            else if(x-1>=a) return x-1;
        }
    }

    function oddRandomNumberInRange(a, b) {
        var x = Math.floor(Math.random()*(b-a)+a);
        if(x&1){
            return x;
        }
        else{
            if(x+1<=b) return x+1;
            else if(x-1>=a) return x-1;
        }
    }

    const handleBFS = async () => {
        setRunning(true);
        if (isVisualised) {
            if(isMazeVisualised){
                handleVisitedReset();
            }
            else{
                handleCompleteReset();
            }
        }
        var qu = new Queue();
        visited = [...grid];
        visited[src.x][src.y] = 1;
        qu.enqueue({
            x: src.x,
            y: src.y
        });
        var dist = createGrid(n, m);
        var got = false;
        while (!qu.isEmpty() && got === false) {
            let node = qu.front();
            qu.dequeue();
            for (let i = 0; i < 4; i++) {
                let child_x = node.x + dx[i];
                let child_y = node.y + dy[i];
                if (child_x < 0 || child_x >= grid.length) continue;
                if (child_y < 0 || child_y >= grid[0].length) continue;
                if (visited[child_x][child_y] === 0 || (child_x === dest.x && child_y === dest.y)) {
                    visited[child_x][child_y] = 1;
                    dist[child_x][child_y] = 1 + dist[node.x][node.y];
                    parent[child_x][child_y].par_x = node.x;
                    parent[child_x][child_y].par_y = node.y;
                    if (child_x === dest.x && child_y === dest.y) {
                        got = true;
                        break;
                    }
                    color(child_x, child_y);
                    await new Promise(r => setTimeout(r, 10));
                    qu.enqueue({
                        x: child_x,
                        y: child_y
                    });
                }
            }
        }
        var curr_x = parent[dest.x][dest.y].par_x;
        var curr_y = parent[dest.x][dest.y].par_y;
        if (curr_x === -1 && curr_y === -1) {
            setAlert(true);
            return;
        }
       
        var rev_path = [];
        while (true) {
            if (curr_x === src.x && curr_y === src.y) break;
            rev_path.push({x_cor: curr_x, y_cor: curr_y});
            let tmp = curr_x;
            curr_x = parent[curr_x][curr_y].par_x;
            curr_y = parent[tmp][curr_y].par_y;
        }
        rev_path.reverse();
        for(var i=0; i<rev_path.length;i++){
            retrack(rev_path[i].x_cor, rev_path[i].y_cor);
            await new Promise(r => setTimeout(r, 20));
        }
        setIsVisualised(true);
        setDistance(dist[dest.x][dest.y]);
        setRunning(false);
        restore_buttons();
        setReached(true);
    }

    const handleDFS = () => {
        setRunning(true);
        var obtainedPath = false;
        if (isVisualised) {
            if(isMazeVisualised){
                handleVisitedReset();
            }
            else{
                console.log("else");
                handleCompleteReset();
            }
        }
        var dist = createGrid(n, m);
        var paths = [];
        for(var i=0;i<n;i++){
            for(var j=0;j<m;j++) visited[i][j] = grid[i][j];
        }
        
        const DFShelper = (x_cor, y_cor) => {
            visited[x_cor][y_cor] = 1;
            if (obtainedPath) return;
            for (let i = 0; i < 4; i++) {
                let child_x = x_cor + dx[i];
                let child_y = y_cor + dy[i];
                if (child_x < 0 || child_x >= n) continue;
                if (child_y < 0 || child_y >= m) continue;
                if (visited[child_x][child_y] === 0 || (child_x === dest.x && child_y === dest.y)) {
                    dist[child_x][child_y] = 1 + dist[x_cor][y_cor];
                    parent[child_x][child_y].par_x = x_cor;
                    parent[child_x][child_y].par_y = y_cor;
                    if (child_x === dest.x && child_y === dest.y) {
                        obtainedPath = true;
                        return;
                    }
                    paths.push({
                        x: child_x,
                        y: child_y
                    });
                    DFShelper(child_x, child_y);
                }
            }
        }
        DFShelper(src.x, src.y);
        const show = async () => {
            for (let i = 0; i < paths.length; i++) {
                color(paths[i].x, paths[i].y);
                await new Promise(r => setTimeout(r, 10));
            }
            var curr_x = parent[dest.x][dest.y].par_x;
            var curr_y = parent[dest.x][dest.y].par_y;
            if (curr_x === -1 && curr_y === -1) {
                setAlert(true);
                return;
            }
            var rev_path = [];
            while (true) {
                if (curr_x === src.x && curr_y === src.y) break;
                rev_path.push({x_cor: curr_x, y_cor: curr_y});
                let tmp = curr_x;
                curr_x = parent[curr_x][curr_y].par_x;
                curr_y = parent[tmp][curr_y].par_y;
            }
            rev_path.reverse();
            for(let i=0; i<rev_path.length;i++){
                retrack(rev_path[i].x_cor, rev_path[i].y_cor);
                await new Promise(r => setTimeout(r, 20));
            }
            setReached(true);
            setRunning(false);
            restore_buttons();
            setDistance(dist[dest.x][dest.y]);
            setIsVisualised(1);
        }
        show();
    }

    const handleMazeGeneration = () => {
        setRunning(true);
        if (isMazeVisualised || isVisualised) {
            handleCompleteReset();
        }
        var paths = [];
        const recursiveDivisionMaze = (rowStart, rowEnd, colStart, colEnd, orientation, surroundingWalls) => {
            if (rowEnd < rowStart || colEnd < colStart) {
                return;
            }

            if (!surroundingWalls) {
                for (let r = 0; r < n; r++) {
                    for (let c = 0; c < m; c++) {
                        if (r === 0 || c === 0 || r === n - 1 || c === m - 1) {
                            paths.push({
                                x_cor: r,
                                y_cor: c
                            });
                            visited[r][c] = 3;
                        }
                    }
                }
                surroundingWalls = true;
            }

            if (orientation === "horizontal") {
                let rowSelectedForWalls = evenRandomNumberInRange(rowStart, rowEnd);
                let colSelectedForHole = oddRandomNumberInRange(colStart-1, colEnd+1);
                for (let i = colStart - 1; i <= colEnd + 1; i++) {
                    if (i !== colSelectedForHole) {
                        paths.push({
                            x_cor: rowSelectedForWalls,
                            y_cor: i
                        });
                        visited[rowSelectedForWalls][i] = 3
                    }
                }
                if (rowSelectedForWalls - 2 - rowStart > colEnd - colStart) {
                    recursiveDivisionMaze(rowStart, rowSelectedForWalls - 2, colStart, colEnd, orientation, surroundingWalls);
                } 
                else {
                    recursiveDivisionMaze(rowStart, rowSelectedForWalls - 2, colStart, colEnd, "vertical", surroundingWalls);
                }
                if (rowEnd - (rowSelectedForWalls + 2) > colEnd - colStart) {
                    recursiveDivisionMaze(rowSelectedForWalls + 2, rowEnd, colStart, colEnd, orientation, surroundingWalls);
                } 
                else {
                    recursiveDivisionMaze(rowSelectedForWalls + 2, rowEnd, colStart, colEnd, "vertical", surroundingWalls);
                }
            } 
            else {
                let colSelectedForWalls = evenRandomNumberInRange(colStart, colEnd);
                let rowSelectedForHole = oddRandomNumberInRange(rowStart-1, rowEnd+1);
                for (var i = rowStart - 1; i <= rowEnd + 1; i++) {
                    if (i !== rowSelectedForHole) {
                        paths.push({
                            x_cor: i,
                            y_cor: colSelectedForWalls
                        });
                        visited[i][colSelectedForWalls] = 3;
                    }
                }
                if (rowEnd - rowStart > colSelectedForWalls - 2 - colStart) {
                    recursiveDivisionMaze(rowStart, rowEnd, colStart, colSelectedForWalls - 2, "horizontal", surroundingWalls);
                } 
                else {
                    recursiveDivisionMaze(rowStart, rowEnd, colStart, colSelectedForWalls - 2, orientation, surroundingWalls);
                }
                if (rowEnd - rowStart > colEnd - (colSelectedForWalls + 2)) {
                    recursiveDivisionMaze(rowStart, rowEnd, colSelectedForWalls + 2, colEnd, "horizontal", surroundingWalls);
                } 
                else {
                    recursiveDivisionMaze(rowStart, rowEnd, colSelectedForWalls + 2, colEnd, orientation, surroundingWalls);
                }
            }
        };
        recursiveDivisionMaze(2, n - 3, 2, m - 3, "horizontal", false);
        const show = async () => {
            for (let i = 0; i < paths.length; i++) {
                wall(paths[i].x_cor, paths[i].y_cor);
                await new Promise(r => setTimeout(r, 10));
            }
            setRunning(false);
            restore_buttons();
            setIsMazeVisualised(true);
        }
        show();
    }

    const handleHorizontalSkew = () => {
        let required = 65;
        setRunning(true);
        if (isMazeVisualised || isVisualised) {
            handleCompleteReset();
        }
        var paths = [];
        const horizontalSkew = (rowStart, rowEnd, colStart, colEnd, orientation, surroundingWalls) => {
            if (rowEnd < rowStart || colEnd < colStart) {
                return;
            }

            if (!surroundingWalls) {
                for (let r = 0; r < n; r++) {
                    for (let c = 0; c < m; c++) {
                        if (r === 0 || c === 0 || r === n - 1 || c === m - 1) {
                            paths.push({
                                x_cor: r,
                                y_cor: c
                            });
                            visited[r][c] = 3;
                        }
                    }
                }
                surroundingWalls = true;
            }

            if (orientation === "horizontal") {
                let rowSelectedForWalls = evenRandomNumberInRange(rowStart, rowEnd);
                let colSelectedForHole = oddRandomNumberInRange(colStart-1, colEnd+1);
                for (let i = colStart - 1; i <= colEnd + 1; i++) {
                    if (i !== colSelectedForHole) {
                        paths.push({
                            x_cor: rowSelectedForWalls,
                            y_cor: i
                        });
                        visited[rowSelectedForWalls][i] = 3
                    }
                }
                let percentage = Math.floor(Math.random()*100);
                if (rowSelectedForWalls - 2 - rowStart > colEnd - colStart) {
                    horizontalSkew(rowStart, rowSelectedForWalls - 2, colStart, colEnd, orientation, surroundingWalls);
                } 
                else {
                    if(percentage >= required){
                        horizontalSkew(rowStart, rowSelectedForWalls - 2, colStart, colEnd, "vertical", surroundingWalls);
                    }
                    else{
                        horizontalSkew(rowStart, rowSelectedForWalls - 2, colStart, colEnd, orientation, surroundingWalls);
                    }
                }
                if (rowEnd - (rowSelectedForWalls + 2) > colEnd - colStart) {
                    horizontalSkew(rowSelectedForWalls + 2, rowEnd, colStart, colEnd, orientation, surroundingWalls);
                } 
                else {
                    if(percentage >= required){
                        horizontalSkew(rowSelectedForWalls + 2, rowEnd, colStart, colEnd, "vertical", surroundingWalls);
                    }
                    else{
                        horizontalSkew(rowSelectedForWalls + 2, rowEnd, colStart, colEnd, orientation, surroundingWalls);
                    }
                }
            } 
            else {
                let colSelectedForWalls = evenRandomNumberInRange(colStart, colEnd);
                let rowSelectedForHole = oddRandomNumberInRange(rowStart-1, rowEnd+1);
                for (var i = rowStart - 1; i <= rowEnd + 1; i++) {
                    if (i !== rowSelectedForHole) {
                        paths.push({
                            x_cor: i,
                            y_cor: colSelectedForWalls
                        });
                        visited[i][colSelectedForWalls] = 3;
                    }
                }
                let percentage = Math.floor(Math.random()*100);
                if (rowEnd - rowStart > colSelectedForWalls - 2 - colStart) {
                    horizontalSkew(rowStart, rowEnd, colStart, colSelectedForWalls - 2, "horizontal", surroundingWalls);
        
                } 
                else {
                    if(percentage >= required){
                        horizontalSkew(rowStart, rowEnd, colStart, colSelectedForWalls - 2, orientation, surroundingWalls);
                    }
                    else{
                        horizontalSkew(rowStart, rowEnd, colStart, colSelectedForWalls - 2, "horizontal", surroundingWalls);
                    }
                }
                if (rowEnd - rowStart > colEnd - (colSelectedForWalls + 2)) {
                    horizontalSkew(rowStart, rowEnd, colSelectedForWalls + 2, colEnd, "horizontal", surroundingWalls);
                } 
                else {
                    if(percentage >= required){
                        horizontalSkew(rowStart, rowEnd, colSelectedForWalls + 2, colEnd, orientation, surroundingWalls);
                    }
                    else{
                        horizontalSkew(rowStart, rowEnd, colSelectedForWalls + 2, colEnd, "horizontal", surroundingWalls);
                    }
                }
            }
        };
        horizontalSkew(2, n - 3, 2, m - 3, "horizontal", false);
        const show = async () => {
            for (let i = 0; i < paths.length; i++) {
                wall(paths[i].x_cor, paths[i].y_cor);
                await new Promise(r => setTimeout(r, 10));
            }
            setRunning(false);
            restore_buttons();
            setIsMazeVisualised(true);
        }
        show();
    }

    const handleVerticalSkew = () => {
        let required = 65;
        setRunning(true);
        if (isMazeVisualised || isVisualised) {
            handleCompleteReset();
        }
        var paths = [];
        const verticalSkew = (rowStart, rowEnd, colStart, colEnd, orientation, surroundingWalls) => {
            if (rowEnd < rowStart || colEnd < colStart) {
                return;
            }

            if (!surroundingWalls) {
                for (let r = 0; r < n; r++) {
                    for (let c = 0; c < m; c++) {
                        if (r === 0 || c === 0 || r === n - 1 || c === m - 1) {
                            paths.push({
                                x_cor: r,
                                y_cor: c
                            });
                            visited[r][c] = 3;
                        }
                    }
                }
                surroundingWalls = true;
            }

            if (orientation === "horizontal") {
                let rowSelectedForWalls = evenRandomNumberInRange(rowStart, rowEnd);
                let colSelectedForHole = oddRandomNumberInRange(colStart-1, colEnd+1);
                for (let i = colStart - 1; i <= colEnd + 1; i++) {
                    if (i !== colSelectedForHole) {
                        paths.push({
                            x_cor: rowSelectedForWalls,
                            y_cor: i
                        });
                        visited[rowSelectedForWalls][i] = 3
                    }
                }
                let percentage = Math.floor(Math.random()*100);
                if (rowSelectedForWalls - 2 - rowStart > colEnd - colStart) {
                    if(percentage >= required){
                        verticalSkew(rowStart, rowSelectedForWalls - 2, colStart, colEnd, orientation, surroundingWalls);
                    }
                    else{
                        verticalSkew(rowStart, rowSelectedForWalls - 2, colStart, colEnd, "vertical", surroundingWalls);
                    }
                } 
                else {
                    verticalSkew(rowStart, rowSelectedForWalls - 2, colStart, colEnd, "vertical", surroundingWalls);
                }
                if (rowEnd - (rowSelectedForWalls + 2) > colEnd - colStart) {
                    if(percentage >= required){
                        verticalSkew(rowSelectedForWalls + 2, rowEnd, colStart, colEnd, orientation, surroundingWalls);
                    }
                    else{
                        verticalSkew(rowSelectedForWalls + 2, rowEnd, colStart, colEnd, "vertical", surroundingWalls);
                    }
                } 
                else {
                    verticalSkew(rowSelectedForWalls + 2, rowEnd, colStart, colEnd, "vertical", surroundingWalls);
                }
            } 
            else {
                let colSelectedForWalls = evenRandomNumberInRange(colStart, colEnd);
                let rowSelectedForHole = oddRandomNumberInRange(rowStart-1, rowEnd+1);
                for (var i = rowStart - 1; i <= rowEnd + 1; i++) {
                    if (i !== rowSelectedForHole) {
                        paths.push({
                            x_cor: i,
                            y_cor: colSelectedForWalls
                        });
                        visited[i][colSelectedForWalls] = 3;
                    }
                }
                let percentage = Math.floor(Math.random()*100);
                if (rowEnd - rowStart > colSelectedForWalls - 2 - colStart) {
                    if(percentage >= required){
                        verticalSkew(rowStart, rowEnd, colStart, colSelectedForWalls - 2, "horizontal", surroundingWalls);
                    }
                    else{
                        verticalSkew(rowStart, rowEnd, colStart, colSelectedForWalls - 2, orientation, surroundingWalls);
                    }
                } 
                else {
                    verticalSkew(rowStart, rowEnd, colStart, colSelectedForWalls - 2, orientation, surroundingWalls);
                }
                if (rowEnd - rowStart > colEnd - (colSelectedForWalls + 2)) {
                    if(percentage >= required){
                        verticalSkew(rowStart, rowEnd, colSelectedForWalls + 2, colEnd, "horizontal", surroundingWalls);
                    }
                    else{
                        verticalSkew(rowStart, rowEnd, colSelectedForWalls + 2, colEnd, orientation, surroundingWalls);
                    }
                } 
                else {
                    verticalSkew(rowStart, rowEnd, colSelectedForWalls + 2, colEnd, orientation, surroundingWalls);
                }
            }
        };

           
        verticalSkew(2, n - 3, 2, m - 3, "vertical", false);
        const show = async () => {
            for (let i = 0; i < paths.length; i++) {
                wall(paths[i].x_cor, paths[i].y_cor);
                await new Promise(r => setTimeout(r, 10));
            }
            setRunning(false);
            restore_buttons();
            setIsMazeVisualised(true);
        }
        show();
    }

    const handleRandomMazeGenerate = async() => {
        setRunning(true);
        if (isMazeVisualised || isVisualised) {
            handleCompleteReset();
        }
      
        for(var i=0;i<n;i++){
            for(var j=0;j<m;j++){
                if(Math.floor(Math.random()*100) <=20){
                    visited[i][j] = 3;
                    wall(i,j);
                    await new Promise(r => setTimeout(r, 10));
                }
            }
        }
        setRunning(false);
        restore_buttons();
        setIsMazeVisualised(true);
    }

    const handleOkClick = () => {
        let button1 = document.getElementById('1');
        let button2 = document.getElementById('2');
        let button3 = document.getElementById('3');
        let button4 = document.getElementById('4');
        let button5 = document.getElementById('5');
        
        button1.disabled = false;
        button1.className = 'algobutton'
        
        button2.disabled = false;
        button2.className = 'mazebutton'
        
        button3.className = 'pathReset';
        button3.disabled = false;
        
        button4.className = 'gridReset'
        button4.disabled = false;
        
        button5.disabled = false;
        button5.className = 'visual';
        button5.textContent = "Visualize";
        setAlert(false);
        setIsMazeVisualised(true);
        setIsVisualised(true);
        setRunning(false);
    }

    return (
        <>
        <Navbar algo = {algo} changeAlgo = { changeAlgo} 
                mazeAlgo = { mazeAlgo } changeMazeAlgo = {changeMazeAlgo}
                handleBFS = {handleBFS}
                handleDFS = {handleDFS}
                handleMazeGeneration = {handleMazeGeneration}
                handleHorizontalSkew = {handleHorizontalSkew}
                handleVerticalSkew = {handleVerticalSkew}
                handleRandomMazeGenerate = {handleRandomMazeGenerate}
                handleVisitedReset = {handleVisitedReset}
                handleCompleteReset = {handleCompleteReset}
        />
        
        { alert && 
            <div className="not-found">
                <img src={pageNotFound} alt="pageNotFound" height="50px"></img>
                <p className='not-found-text'>Path Not Found</p>
                <button className="ok" onClick={handleOkClick}>OK</button>
            </div>
        }

        <Grid grid = {grid} setGrid = {setGrid}
              distance = {distance} setDistance = {setDistance}
              reached = {reached} setReached = {setReached} 
              wall = {wall}
              isVisualised = {isVisualised }
              running = {running}
              src= {src} setSrc = {setSrc}
              dest = {dest} setDest = {setDest}
              setIsMazeVisualised = {setIsMazeVisualised}
        />
        <Footer />
        </>
    )
}

var createGrid = (row, col) => {
    var board = new Array(row);
    for (let i = 0; i < row; i++) {
        board[i] = new Array(col);
        for (let j = 0; j < col; j++) board[i][j] = 0;
    }
    return board;
}
