import { useState } from 'react';
import './Grid.css'

export default function Grid(props) {   
    const [hold, setHold] = useState(false);

    const getCellType = (visit, rowInd, colInd) => {
        var cellType = 'cell';
        if (rowInd === props.src.x && colInd === props.src.y) {
            cellType = 'source';
        } 
        else if(rowInd === props.dest.x && colInd === props.dest.y){
            cellType = 'dest';
        }
        else if (visit[rowInd][colInd] === 1) {
            cellType = 'visited';
        } else if (visit[rowInd][colInd] === 2) {
            cellType = 'retrack';
        } else if (visit[rowInd][colInd] === 3) {
            cellType = 'wall';
        }
        return cellType;
    }

    const handleClickOnGrid = (r,c) => {
        let cell = document.getElementById(r + "-" + c);
        if(cell && !props.isVisualised && !props.running && hold) {
            props.wall(r,c);
            props.setIsMazeVisualised(true);
        }
    }

    const handleToggle = () => setHold(!hold);

    return (
        <div className="mainGrid">
            <h2 className="distance">Distance : {(props.reached===true?props.distance:'âˆž')} </h2>
            <div className="grids">
            {props.grid.map((row, rowIdx) => (
            <div id={rowIdx} className="row">
                {row.map((cellValue, cellIdx) => {
                const cellType = getCellType(
                    props.grid,
                    rowIdx,
                    cellIdx
                );
                return <div className={cellType} id={rowIdx +"-" +cellIdx} onClick={handleToggle} onMouseOver={() => handleClickOnGrid(rowIdx, cellIdx)}> </div>;
                    
                })}
            </div>
            ))}
            </div>
        </div>
    )
}


