import './App.css';
import Main from './Main/Main';
import NotFound from './NotFound/NotFound';
import Tutorial from './Tutorial/Tutorial';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';

function App() {
  
  return (
    <Router>
      <div className="App"> 
          <Switch>
            <Route exact path ="/">
              <Tutorial />
            </Route>
            <Route path ="/playground">
              <Main />
            </Route>
            <Route path ="*">
              <NotFound />
            </Route>
          </Switch>
      </div>
    </Router>
  );
}

export default App;
